<template>
  <div v-click-outside="() => menu = false">
      <a
        class="icon-btn is--template-border large-blur"
        
        @click="menu = !menu"
      >
        <i
          :class="desmosGrid === 'normal' ? 'fa fa-columns': desmosGrid === 'vertical' ? 'fas fa-sort-amount-down': 'fa fa-window-maximize'"
          style="font-size: 2rem;"
        ></i>
      </a>
      <div
        v-if="menu"
        class="sub-menu"
      >
        <a
          v-if="desmosUrls.length > 1"
          class="icon-btn is--template-border large-blur"
          @click="$emit('on-set-desmos-display', 'normal'), menu = !menu"
        >
          <i style="font-size: 2rem;" class="fa fa-columns"></i>
        </a>
        <a
          class="icon-btn is--template-border large-blur"
          @click="$emit('on-set-desmos-display', 'vertical'), menu = !menu"
        >
          <i style="font-size: 2rem;" class="fas fa-sort-amount-down"></i>
        </a>
        <a
          class="icon-btn is--template-border large-blur"
          @click="$emit('on-set-desmos-display', 'desmos'), menu = !menu"
        >
          <i style="font-size: 2rem;" class="fa fa-window-maximize"></i>
        </a>
      </div>
  </div>
</template>

<script>
export default {
    name: "DesmosDisplayComp",
    props: {
        desmosGrid: {
            type: String,
            defaultValue: 'normal'
        },
        desmosUrls: {
            type: Array,
            defaultValue: [1]
        }
    },
    data: () => ({
      menu: false
    }),
    methods: {

    },
    directives: {
    "click-outside": {
      bind: function (el, binding, vNode) {
        // Provided expression must evaluate to a function.
        if (typeof binding.value !== "function") {
          const compName = vNode.context.name;
          let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`;
          if (compName) {
            warn += `Found in component '${compName}'`;
          }

          console.warn(warn);
        }
        // Define Handler and cache it on the element
        const bubble = binding.modifiers.bubble;
        const handler = (e) => {
          if (bubble || (!el.contains(e.target) && el !== e.target)) {
            binding.value(e);
          }
        };
        el.__vueClickOutside__ = handler;

        // add Event Listeners
        document.addEventListener("click", handler);
      },

      unbind: (el) => {
        // Remove Event Listeners
        document.removeEventListener("click", el.__vueClickOutside__);
        el.__vueClickOutside__=null;
      },
    },
  },
}
</script>

<style scoped>
.sub-menu {
  position: absolute;
  z-index: 100;
  padding: 2px;
  border-top-style: solid;
  border-top-width: 1px;
  border-top-color: hsla(0, 0.00%, 24.67%, 1.00);
  border-right-style: solid;
  border-right-width: 1px;
  border-right-color: hsla(0, 0.00%, 24.67%, 1.00);
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: hsla(0, 0.00%, 24.67%, 1.00);
  border-left-style: solid;
  border-left-width: 1px;
  border-left-color: hsla(0, 0.00%, 24.67%, 1.00);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
</style>